import styled from "styled-components";

export const ServiceContainer = styled.div`
  width: 1000px;
  margin: 0 auto;
  padding: 80px 0;

  @media screen and (max-width: 1200px) {
    width: 95%;
  }
`;

export const ServiceH2 = styled.h2`
  font-family: "Russo One", sans-serif;
  font-size: 36px;
  text-align: center;
  margin-bottom: 20px;

  @media screen and (max-width: 768px) {
    font-size: 26px;
  }
`;

export const ServiceP = styled.p`
  color: #4b5156;
  font-family: "Roboto Mono", monospace;
  text-align: center;

  @media screen and (max-width: 768px) {
    text-align: justify;
    font-size: 14px;
  }
`;

export const ServiceCardWrapper = styled.div`
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 30px 0 0 0;

  /* @media screen and (max-width: 768px) {
    margin: 20px 0;
  } */
`;

export const ServiceCard = styled.div`
  flex-grow: 1;
  /* background-color: #48a7ff; */
  padding: 20px 20px;
  /* -webkit-box-shadow: 13px 13px 7px -7px rgba(0, 0, 0, 0.17);
  -moz-box-shadow: 13px 13px 7px -7px rgba(0, 0, 0, 0.17);
  box-shadow: 13px 13px 7px -7px rgba(0, 0, 0, 0.17); */

  /* box-shadow: -15px -15px 15px rgba(255, 255, 255, 0),
    15px 15px 15px rgba(0, 0, 0, 0), inset 4px 4px 5px rgba(255, 255, 255, 0.8),
    inset -4px -4px 5px rgba(0, 0, 0, 0.05), 0 50px 40px rgba(0, 0, 0, 0.05); */

  /* box-shadow: rgba(0, 0, 0, 0.05) 0px 10px 20px, rgba(0, 0, 0, 0.2) 0px 0px 1px; */
  box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  display: flex;
  justify-content: center;
  margin: 10px;

  @media screen and (max-width: 768px) {
    /* margin: 20px 0; */
    justify-content: start;
  }
`;

export const ServiceCardImage = styled.div`
  img {
    width: 100px;
    height: 100px;
    object-fit: cover;
  }

  @media screen and (max-width: 768px) {
    img {
      width: 70px;
      height: 70px;
    }
  }
`;

export const ServiceCardText = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 10px;
  font-size: 20px;
  color: black;
  font-family: "Russo One", sans-serif;
  font-weight: 500;

  @media screen and (max-width: 768px) {
    font-size: 16px;
    justify-content: start;
  }
`;
